// Color system
$white:       #ffffff;
$pink:        #FBF2F2;
$red:         #AA0601;
$red-dark:    #9F1213;
$black:       #000000;
$dark:        #212529;
$brown:       #650300;
$gray:        #707070;
$gray-light:  #CED4DA;

$colors: (
  "pink":   $pink,
  "red":    $red,
  "dark":   $dark,
  "brown":  $brown,
  "white":  $white,
  "black":  $black,
  "gray":   $gray,
);

$primary:   $red;
$secondary: $brown;
$theme-colors: (
  "primary":    $primary
);

// Spacing
$spacers: (
  "0":   0,
  "xs":  .25rem,   // 4
  "s":   .75rem,   // 12
  "m":   2.25rem,  // 36
  "l":   4.5rem,   // 72
  "xl":  6rem,     // 96
  "xxl": 8rem,     // 128
  "1":   0.5rem,   // 8
  "2":   1rem,     // 16
  "3":   1.25rem,  // 20
  "4":   1.5rem,   // 24
  "5":   1.875rem, // 30
  "6":   2rem,     // 32
  "7":   2.5rem,   // 40
  "8":   3rem,     // 48
  "9":   3.5rem,   // 56
  "10":  3.75rem,  // 60
  "11":  5rem,     // 80
);

// Grid breakpoints
$grid-breakpoints: (
  xs: 0,
  xl: 1200px,
  lg: 992px,
  md: 767px,
  sm: 576px,
);

// Grid containers
$container-max-widths: (
  xl: 1140px,
  lg: 960px,
  md: 720px,
  sm: 540px,
);

$include-column-box-sizing:   true !default;
$enable-grid-classes:         true !default;
$grid-columns:                12 !default;
$grid-gutter-width:           1.5rem !default;
$grid-row-columns:            6 !default;
$gutters: $spacers !default;

// Container padding
$container-padding-x: $grid-gutter-width / 2 !default;

// Typography
$variable-prefix:             bs- !default;

// Gradient
$gradient: linear-gradient(180deg, rgba($white, .15), rgba($white, 0)) !default;

$font-family-sans-serif:      -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;
$font-family-monospace:       SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;

// Font, line-height, and color for body text, headings, and more.
$font-family-noto-sans: "Noto Sans TC", sans-serif;
$font-family-roboto:    "Roboto", sans-serif;

$font-weights: (
  "light":    300,
  "regular":  400,
  "medium":   500,
  "bold":     700,
  "black":    900,
);

$line-heights: (
  "0":  1.5,
  
);

$font-sizes: (
  "s":   .875rem,   // 14
  "m":   1.1875rem, // 19
  "xl":  4rem,      // 64
  "xxl": 6.25rem,   // 100
  "1":   1rem,      // 16
  "2":   1.25rem,   // 20
  "3":   1.5rem,    // 24
  "4":   2rem,      // 32
  "5":   2.25rem,   // 36
  "6":   3rem,      // 48
);

$max-widths: (
  "s": 255px
);

// button
$btn-padding-y:         .375rem;
$btn-padding-x:         .75rem;
$btn-font-size:         1rem;
$btn-border-radius:     .25rem;
$btn-padding-y-sm:      .25rem;
$btn-padding-x-sm:      .5rem;
$btn-font-size-sm:      .875rem;
$btn-border-radius-sm:  .2rem;
$btn-padding-y-lg:      .5rem;
$btn-padding-x-lg:      1rem;
$btn-font-size-lg:      1.25rem;
$btn-border-radius-lg:  .3rem;